  --answer2-100: #36A6DF; 

  --answer3-0: #EDC21A;
  --answer3-100: #F7F0C4; 

  --bottom-61: #403B6D;
  --bottom-74: #413A6A;
  --bottom-82: #453A61;
  --bottom-90: #4D3A51;
  --bottom-97: #573A3C;
  --bottom-100: #5E3A30;
}

html,
body {
  margin: 0;
  height: 100%;
}

body {
  min-height: 100dvh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-home {
  margin: 0;
  padding: 0;
  background-color: #6fb9b0;
  background-image: radial-gradient(
      at 0% 0%,
      rgba(255, 230, 80, 0.8) 0px,
      transparent 50%
    ),
    radial-gradient(at 100% 0%, rgba(255, 255, 255, 0.9) 0px, transparent 50%),
    radial-gradient(at 0% 100%, rgba(255, 250, 180, 0.6) 0px, transparent 50%),
    radial-gradient(at 100% 100%, rgba(80, 160, 150, 0.4) 0px, transparent 50%);
  background-repeat: no-repeat;
  background-size: cover;
}

/* 背景顏色 */
.bg-end-0 {
  background: linear-gradient(
    to bottom,
    var(--answer0-0),
    var(--answer0-100)
  );
}
.bg-end-0.result {
  background: linear-gradient(
    to bottom,
    var(--answer0-0) 0%,
    var(--answer0-100) 50%,

    var(--answer0-100) 50%,
    var(--bottom-61) calc(50% + 0.61 * 50%),
    var(--bottom-74) calc(50% + 0.74 * 50%),
    var(--bottom-82) calc(50% + 0.82 * 50%),
    var(--bottom-90) calc(50% + 0.90 * 50%),
    var(--bottom-97) calc(50% + 0.97 * 50%),
    var(--bottom-100) 100%
  );
}

