/* ------------ */

/* 按鈕效果 */
.button:hover {
  transition-duration: 0.1s;
  background-color: #ffffff2c;
  filter: blur(1px);
}

.button:after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.5s;
  box-shadow: 0 0 10px 40px white;
}

.button:active:after {
  box-shadow: 0 0 0 0 white;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top: 0;
  opacity: 1;
  transition: 0s;
}

.button:active {
  top: 1px;
}

/* 水波容器：在最上層，但不擋按鈕點擊 */
.bg-ripple-container {
  position: fixed;
  inset: 0;
  pointer-events: none;
  overflow: hidden;
  z-index: 999; /* 保證可以看到 */
}

/* 單一水波 */
.bg-ripple,
.bg-ripple-2,
.bg-ripple-3 {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  opacity: 0.55;
  mix-blend-mode: screen;
}

/* 三層不同顏色 */
.bg-ripple {
  background: rgba(255, 255, 255, 0.7);
  animation: rippleAni 0.9s ease-out forwards;
}

.bg-ripple-2 {
  background: rgba(173, 238, 255, 0.6);
  animation: rippleAni 1.1s ease-out forwards;
}

.bg-ripple-3 {
  background: rgba(255, 255, 255, 0.4);
  animation: rippleAni 1.3s ease-out forwards;
}

@keyframes rippleAni {
  100% {
    transform: scale(15);
    opacity: 0;
  }
}

/* 最後淡出層（只出現在最後，不阻擋水波） */
.fade-to-next {
  position: fixed;
  inset: 0;
  background: white;
  opacity: 0;
  pointer-events: none;
  z-index: 998; /* 注意：比 ripple 小！ */
  animation: fadeNext 0.5s ease-out forwards;
}

@keyframes fadeNext {
  to {
    opacity: 1;
  }
}
/* -------- */

/* 自訂透明極簡 range */
.range-clean {
  -webkit-appearance: none;
  appearance: none;
  background: transparent; /* 背景透明 */
  width: 100%;
}

/* 軌道（整條線） */
.range-clean::-webkit-slider-runnable-track {
  height: 14px;
  background: rgba(255, 255, 255, 0); /* 白框效果 */
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 1);
}

.range-clean::-moz-range-track {
  height: 14px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 1);
}

/* ---- FILLED TRACK（滑過的部分）---- */
.range-clean::-webkit-slider-runnable-track {
  background: rgb(255, 255, 255);
}

/* 隱藏原生 thumb（白點） */
.range-clean::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 14px;
  background: white;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -1px; /* 調整 thumb 垂直位置 */
}

/* Firefox */
.range-clean::-moz-range-progress {
  background: white; /* 白色進度條 */
  height: 14px;
}

/* Chrome 進度條實作 */
.range-clean::-webkit-slider-runnable-track {
  background: linear-gradient(
    to right,
    white var(--value, 0%),
    transparent var(--value, 0%)
  );
}

/* 去掉 focus 外框 */
.range-clean:focus {
  outline: none;
}

